import 'material-design-icons-iconfont/dist/material-design-icons.css'

<template>
  
  <div class="pa-3" id="tester">
   
      <mySnackBar  v-bind:MySnackBar="this.MySnackBar"></mySnackBar>
 
      <vue-easy-lightbox :visible = "lightBoxVisible" :imgs = "lightBoxImage"  @hide = "hideLightBoxImage"></vue-easy-lightbox>
      
      <label class="d-none">
         <input id="file-input"         v-on:change="imageChange"   type="file" accept="image/*" capture >
      </label>
      
      <label class="d-none">
          <input id="file-input_gallery" v-on:change="imageChangeGallery" type="file" accept="image/x-png,image/jpeg,image/gif">
      </label>
      
      <v-container>
        <v-row class="pt-2 pa-1 mb-2 d-flex justify-space-between align-baseline">
                
            <div>Engineer Meter Reading</div>
              
            <div>   
              <v-btn  @click="meterReadUpload" class="mx-2" fab medium color="success">
                <v-icon dark>mdi-upload-box</v-icon>
              </v-btn>
            </div>

        </v-row>
      </v-container>

      <v-row class="mt-0" justify="center">
       <v-expansion-panels class="expansion_panels">
    

        <!-- customerComponent -->
          <customer v-bind:customer = "customer" />
        <!-- customerComponent -->

         <!-- Meter Reading -->
          <v-expansion-panel class="expansion_pane mb-1"> 
         <v-expansion-panel-header>Meter Read</v-expansion-panel-header>
         <v-expansion-panel-content>

              <v-container >
    
               <v-row dense>

                     <v-col dense cols="12" sm="4">
                         <v-text-field clearable outlined dense v-model="meter.identifier"    label="Serial Number"></v-text-field>
                     </v-col>

                     <v-col dense cols="12" sm="4">
                         <v-text-field  readonly outlined dense v-model="meter.lastReading"    label="Last Reading"></v-text-field>
                     </v-col>

                     <v-col dense cols="12" sm="4">
                        <v-text-field  clearable outlined dense v-model="meter.currentReading"    label="Current Reading"></v-text-field>
                     </v-col>

              </v-row>

              <v-row dense>

                          <v-col cols="12">
                             <v-textarea clearable clear-icon="mdi-close-circle" outlined v-model=  "meter.comments" label="Comments:"></v-textarea>
                          </v-col>
              
                          <v-col cols="12">
                            <v-card>

                                <v-card-title>Meter Image</v-card-title>
                
                                <v-img
                                      height        = "250"
                                      :src          = "meter.imageURL" 
                                      :contain      = "true"
                                      @click        = "showLightBoxImage()"
                                      class         = "white--text align-end"
                                      width         = "100%"
                                      >
                                </v-img>
                                
                                <v-divider class="my-2 mx-2"></v-divider>

                                <v-card-actions>
                              
                                      <v-btn text color="primary" @click="takeImageCamera()">Camera
                                      </v-btn>
                                
                                      <v-btn text color="primary" @click="takeImageGallery()">Gallery
                                      </v-btn>
                                                        
                                      <v-spacer></v-spacer>

                                      <v-btn text color="warning" @click="resetImage()">Reset
                                      </v-btn>
    
                                </v-card-actions>      

                            </v-card>
                          </v-col>

              </v-row>
              

              </v-container >


        </v-expansion-panel-content>
          </v-expansion-panel>
        <!-- end Meter Reading -->

        <!-- last one for space-->
          <v-expansion-panel class="expansion_panel mb-2"> 
          </v-expansion-panel>
        <!-- last one for space-->

      </v-expansion-panels>
     </v-row>

  </div>

</template>

<script>


import {store}   from '../../main.js'
import Localbase from 'localbase'

export default {

   name     : "meterRead",
   formName : 'meterRead',

    destroyed () {
       
          store.consoleLog( "meterRead form destroyed");

           this.customer            = JSON.parse( localStorage.getItem( "equalResponseCustomer") );
           this.engineer            = JSON.parse( localStorage.getItem( "equalResponseEngineer") );
           this.meter.engineer      = this.engineer['ENGINEER_ID'];
           this.meter.customer      = this.customer['ACCOUNT'];

           this.createSnapShot();
    },
      
    created    (){ 
 
         store.consoleLog( "meterRead form created");

         this.customer            = JSON.parse( localStorage.getItem( "equalResponseCustomer") );
         this.engineer            = JSON.parse( localStorage.getItem( "equalResponseEngineer") );
         this.meter.engineer      = this.engineer['ENGINEER_ID'];
         this.meter.customer      = this.customer['ACCOUNT'];

         this.restoreSnapShot();
    },


   methods   : {

              meterReadUpload() {
              },
              
              createSnapShot(){

                let snapShot             =  this.generateSnapShot();
                let db                   =  new Localbase( this.$dbName );

                db.collection( this.meter.collection ).doc( this.meter.customer).set( snapShot )
                .then(response => { 
                   console.log( response)
                })
                .catch(error => {
                   console.log( error)
                })
              },

              restoreSnapShot(){

                let db = new Localbase(  this.$dbName );
                db.collection( this.meter.collection).doc( this.meter.customer).get()
                .then( document => {
                  if ( document != null ) {
                         this.meter   = document
                  }
                })
                .catch( error => {
                   console.log( error)
                });

              },

              generateSnapShot() {
                                let snapShot          =   this.meter;
                                return snapShot;
              },

              showSnackBar( message = "You need to review the form first..."){
                  this.MySnackBar.text          = message;
                  this.MySnackBar.showImage     = "icon";
                  this.MySnackBar.show          = true;
                  setTimeout( () => { this.MySnackBar.show  = false }, 1000)
              },
    
              showUploadingDialog() {
                    this.uploadingDialog.showDialog       = true;
              },
    
              closeUploadingDialog() {
                          setTimeout( () => {
                                  this.uploadingDialog.pdfMessage           = null;
                                  this.uploadingDialog.photosMessage        = null;
                                  this.uploadingdDialog.showDialog          = false;
                          }, 2000)
              },
               
              generateLoggingObject() {
                     return  { 
                                "engineer"    : this.engineer['ENGINEER_ID'].toLowerCase(),
                                "account"     : this.customer['ACCOUNT'].toLowerCase(), 
                                "formName"    : this.formName 
                             };
              },
 
              formReviewUpload    : async function() {
                         
                         const formReviewResponse         = store.retrieveFormReviewResponse();
                         if (  formReviewResponse  ) {
              
                                 formReviewResponse['loggingObject']     = this.generateLoggingObject();
 
                                 this.showUploadingRebrandDialog();
    
                                    this.uploadingRebrandDialog['pdfMessage'] ="Uploading PDF";
                                      const res       = await this.formReviewUpLoadPDF   ( formReviewResponse );
                                    this.uploadingRebrandDialog['pdfMessage'] ="Uploaded PDF";
 
                                    this.uploadingRebrandDialog.photoUpload['status'] = "Uploading Photos...";
                                       const photos    = await this.getformPhotos         ( formReviewResponse );
                                       const uploaded  = await this.uploadFormPhotos      ( photos );
                                    this.uploadingRebrandDialog.photoUpload['status'] = "Uploaded Photos";

                                   store.resetFormReviewResponse();
                                   this.resetPhotoArray(); 
                              
                                   const customer       =  this.customer['ACCOUNT'];
                                   const formName       =  this.formName;
                                   store.removeDocument( customer, formName);

                                this.closeUploadingRebrandDialog();
               
                        } else {
                             this.showSnackBar("You need to review the form first...");
                        }
              }, 
        
              formReviewUpLoadPDF : async function( formReviewResponse ){
            
                                try {
                                     const url   =  this.$restServer.uploadPDFToDropBoxURL;
                                     const res   =  await this.$http.post( url, formReviewResponse );
                                     console.log(res);
                                     return true;
                                } catch (error) {
                                     console.log(error);
                                     return false;
                                }
        
              },

              getformPhotos       : async function( formReviewResponse){

                let customer = formReviewResponse.client;
                let key      = formReviewResponse.formName;

                try {
                  let db          = new Localbase( this.$dbName );
                  let doc         = await db.collection(customer).doc( key ).get();
                  return  doc.formReviewData.photos;
                } catch (error) {
                  console.log(error);
                  return  [];
                }
              },

              uploadPhoto         : async function (photo) {

                const customer   =  this.customer['ACCOUNT'];
                const formName   = "reBrand";          
                const fileName   =  photo.fileName;                

                let   formData     =  new FormData();
                const key          =  photo.fileName;
                const value        =  photo.imageUrl;
                formData.append( key, value);

                let url = `${this.$restServer.photoUpload}/${customer}/${formName}`;
                url     =  url.toLowerCase();

                let response = await  this.$http.post(url, formData, {
                     headers          : { 'Content-Type': 'multipart/form-data'},
                     onUploadProgress : ( progressEvent ) => {
                      
                      let percentCompleted = parseInt(Math.round((progressEvent.loaded / progressEvent.total) * 100));
                      this.uploadingRebrandDialog['loadedBytes']           = progressEvent.loaded;
                      this.uploadingRebrandDialog['totalBytes']            = progressEvent.total;
                      this.uploadingRebrandDialog['percentageCompleted']   = percentCompleted;
                    
                      const message  =  `"${fileName}, total: ${progressEvent.total}, Loaded : ${progressEvent.loaded}, Percentage : ${percentCompleted}`;
                      this.uploadingRebrandDialog.photoUpload[`${fileName}`] = message; 
                      console.log( message);
                    }   

                  }); 
                
                if (response.data.success) {
                    console.log("success", response.data );
                    return true;
                }

              },

              uploadFormPhotos    : async function( photos ) {
  
                const v = await Promise.all(
                  photos.map(  async ( photo ) => {
                      if (photo.isValid) {
                          this.uploadingRebrandDialog.photoUpload[photo.fileName] = 'Upload Started...'; 
                          await this.uploadPhoto(photo);
                      }
                  })
                );
                return v;
              },
      
              formReview(){

                        try {
      
                              this.snapShot( 'create'); 
                              
                              let db             =  new Localbase(  this.$dbName );
                              const customer     =  this.customer['ACCOUNT'];
                              const key          =  this.$options.formName;

                              db.collection(customer).doc( key ).get()
                              .then( (snapShot) => {
                                 if ( snapShot != null ) {
                                        
                                        const photos = snapShot.formReviewData.photos;
                                        snapShot.formReviewData.photos = photos.filter( ( photo ) => {
                                            photo.imageUrl = ( photo.isValid) ? photo.lowRes : "https://www.empty.com";
                                            delete photo.lowRes;
                                            return true;
                                        });

                                        this.$router.push( { name : "FormReview", "params" : snapShot } );
                                }

                              })
                        
                        } catch (e) {
                            alert(e);
                        }
              },


              takeImageGallery     : function() {
                    document.getElementById('file-input_gallery').click();
              },

              takeImageCamera      : function ()  {
                    document.getElementById('file-input').click();
              },

              resetImage           : function ()  {

                    this.meter.imageURL  = this.$restServer['blankPhoto'];
                    this.createSnapShot();

              },


              imageChangeGallery    : async function() {
                                          
                                          try {
                                         
                                              var file     = document.getElementById("file-input_gallery");
                                              if (file.files.length > 0 ) {
                                               
                                                  const photo              = document.getElementById("file-input_gallery").files[0];
                                                  const dataUrl            = await this.getDataUrl( photo  );
                                                  this.meter.imageURL      = dataUrl;
                                                  this.createSnapShot();
                                                  document.getElementById('file-input_gallery').value = null;
                                              }
                      
                                          }catch (error) {
                                              console.log(error); 
                                          }
                         
              },
                      
              imageChange           : async function() {
                                          
                    try {
                   
                        var file     = document.getElementById("file-input");
                        if (file.files.length > 0 ) {
                         
                            const photo            = file.files[0];
                            const dataUrl          = await this.getDataUrl( photo );
                            this.meter.imageURL    = dataUrl;
                            this.createSnapShot();
                            document.getElementById('file-input').value = null;
                        }

                    }catch (error) {
                        console.log(error); 
                    }
   
              },
           
              getDataUrl   : function ( photo) {

                        return new Promise(function(resolve, reject) {
                          
                          const reader = new FileReader();
                          reader.readAsDataURL(photo);
                          
                          reader.addEventListener('load',  function(e) {
                              const dataUrl = e.target.result; 
                              resolve( dataUrl );
                          });

                          reader.addEventListener('error', function(e) {
                             reject(e);
                          });

                        });
              },


              resizeImageLowRes    : function ( photoBase64 ) {
              
                    return new Promise(function(resolve, reject) {

                          const img = new Image();    
                          img.addEventListener('load', function( e ) {

                                          var canvas1    = document.createElement('canvas');
                                          canvas1.width  = 256;
                                          canvas1.height = 256;
                                          var ctx    = canvas1.getContext('2d');
                                          var canvas = ctx.canvas ;
                                          var hRatio = canvas.width  / img.width    ;
                                          var vRatio = canvas.height / img.height  ;
                                          var ratio  = Math.min ( hRatio, vRatio );
                                          var centerShift_x = ( canvas.width - img.width*ratio ) / 2;
                                          var centerShift_y = ( canvas.height - img.height*ratio ) / 2;  
                                          ctx.clearRect(0,0,canvas.width, canvas.height);
                                          ctx.drawImage(img, 0,0, img.width, img.height, centerShift_x,centerShift_y,img.width*ratio, img.height*ratio);
                                          var dataURL  = canvas.toDataURL();
                                          resolve(dataURL);
                          });
                          img.addEventListener('error', function(e) { 
                            reject(e);
                          });
                          
                          img.src = photoBase64;
                  
                    });
                
              },

              resizeImage          : function ( image, ratio) {

                        return new Promise(function(resolve, reject) {
                          
                                const reader = new FileReader();
                                reader.readAsDataURL(image);
                                
                                reader.addEventListener('load',  function(e) {
                                    // Create new image element
                 
                                    const img = new Image();
                                     img.addEventListener('load', function() {
                                          var canvas1    = document.createElement('canvas');
                                          canvas1.width  = 320;
                                          canvas1.height = 240;
                                       
                                          var ctx    = canvas1.getContext('2d');
                                          var canvas = ctx.canvas ;
                                          var hRatio = canvas.width  / img.width    ;
                                          var vRatio = canvas.height / img.height  ;
                                          var ratio  = Math.min ( hRatio, vRatio );
                                          var centerShift_x = ( canvas.width - img.width*ratio ) / 2;
                                          var centerShift_y = ( canvas.height - img.height*ratio ) / 2;  
                                          ctx.clearRect(0,0,canvas.width, canvas.height);
                                          ctx.drawImage(img, 0,0, img.width, img.height, centerShift_x,centerShift_y,img.width*ratio, img.height*ratio);

                                    
                                    // const w       = 640; //ele.width  * ratio;
                                    // const h       = 480; //ele.height * ratio;
                                    // 12-20-2021 lets use 800x600
                                    // 4/10/2024 use 1200x800 and png
                                    // const w       = ele.width;
                                    // const h       = ele.height;
                                    // canvas.width  = w;
                                    // canvas.height = h;
                                    // alert(`canvas width = ${canvas.width}, height = ${canvas.height}`)
                                    // context.drawImage(ele, 0, 0, w, h);
                                    // no resize needed
                                    // context.drawImage(ele, 0, 0, ele.width, ele.height);
                                    // 1.0.19 use jpeg quality
                                    // var dataURL          = canvas.toDataURL("image/jpeg", 1.0);
                                    // 1.0.22 use png
                                        var dataURL  = canvas.toDataURL();
                                        resolve(dataURL);
                                    });
                                     img.src = e.target.result;
                                });

                                reader.addEventListener('error', function(e) {
                                  reject();
                                });
                        });
              },

              showLightBoxImage    : function () {
                    this.lightBoxImage          = this.meter.imageURL;
                    this.lightBoxVisible        = true
              },
     
              hideLightBoxImage    : function() {
                    this.lightBoxImage          =  null;
                    this.lightBoxVisible        =  false
              },

   },

   data() {

    return {
        
        MySnackBar        : { "show" : false, "text" : "Default Text", "showImage" : "icon"},
        loading           : false,
        lightBoxVisible   : false,
        lightBoxImage     : null,
        formName          : "meterRead",
        
        customer          : {},
        salesman          : {},
        engineer          : {} ,

        meter             : {
                              collection          :  "meterRead",
                              engineer            :  "",
                              customer            :  "",
                              identifier          :  "",
                              lastReading         :  "",
                              lastReadingDate     :  "",
                              currentReading      :  "",
                              currentReadingDate  :  "",
                              comments            :  "",
                              imageURL            :  this.$restServer['blankPhoto']
                            },

        };
  }
};


</script>

<style scoped>
h1,h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
.dl_expansion_panel_content>>> .v-expansion-panel-content__wrap {
  padding: 0 !important;
}

.header{
  font-family: 'Roboto';
  font-size: 16px;
  font-weight: bold;
}

input[type=text] {
  width: 100%;
  padding: 8px 20px;
  margin: 2px 0;
  box-sizing: border-box;
  border: 3px solid #ccc;
  -webkit-transition: 0.5s;
  transition: 0.5s;
  outline: none;
}

input[type=text]:focus {
  border: 3px solid #555;
}

div.v-text-field__details {
  display: none;
}

legend.v-label {
  font-size: 16px !important;
}



</style>
       
